import React from "react";
import fetchMessages from "../../actions/message/fetch";
import send from "../../actions/message/send";
import logout from "../../actions/logout";
import array2assoc from "../utils/array2assoc";

export default ChatView =>
  class extends React.Component {
    state = {
      loading: true,
      text: ""
    };

    componentDidMount() {
      this.updateChat();
    }

    updateChat() {
      this.fetchMessages();
    }

    fetchMessages() {
      fetchMessages(this.context.store);
    }

    lineId() {
      return 1;
    }

    send(text) {
      const sendText = text ? text : this.state.text;

      send(this.context.store, sendText, () => {
        this.setState({
          text: ""
        });
      });
    }

    logout() {
      logout(this.context.store.dispatch);
    }

    textChanged(text) {
      this.setState({ text });
    }

    getMessages() {
      return this.props.messages.messages || [];
    }

    renderLoader() {
      return (
        <div className="cssload-thecube">
          <div className="cssload-cube cssload-c1" />
          <div className="cssload-cube cssload-c2" />
          <div className="cssload-cube cssload-c4" />
          <div className="cssload-cube cssload-c3" />
        </div>
      );
    }

    render() {
      if (this.props.chat.loading) return this.renderLoader();

      const incidents = this.props.messages.incidents
        ? array2assoc(this.props.messages.incidents, "firstMessageId")
        : {};

      return (
        <ChatView
          {...this.props}
          state={this.state}
          sending={this.props.messages.sending}
          send={this.send.bind(this)}
          logout={this.logout.bind(this)}
          messages={this.getMessages()}
          incidents={incidents}
          textChanged={this.textChanged.bind(this)}
        />
      );
    }
  };



