import TariffRequest from '../../core/utils/request/TariffRequest';
import fetch from './fetch';

export default (store, tariffId) => {
  new TariffRequest(store, {
    method: 'post',
    path: 'apps/buy',
    data: {
      type: 'HelpMe',
      pricelist: tariffId,
      props: {}
    }
  }, {
    onSuccess: (data) => {
      fetch(store);
      store.dispatch({
        type: 'OPEN_RESULT',
        text: data.descr
      });
    }
  }).send();
};



