import React from 'react';
import fetchTariffs from '../../actions/tariff/fetch';
import buyTariff from '../../actions/tariff/buy';

export default (View, Request) =>
  class extends React.Component {
    componentDidMount() {
      fetchTariffs(this.context.store);
    }
    openTariff(id) {
      this.context.store.dispatch({type:'OPEN_TARIFF', id});
    }
    buyTariff(id) {
      buyTariff(
        this.context.store,
        id
      );
    }
    render() {
      return <View
        {...this.props}
        openTariff={this.openTariff.bind(this)}
        buyTariff={this.buyTariff.bind(this)}
      />;
    }
  }



