import React from 'react';
import Request from '../utils/request/CallerRequest';

export default (View) =>
  class extends React.Component {

    changeDecision() {
      this.context.store.dispatch({
        type: 'SCREEN_CHANGE',
        screen: 'Hangup'
      });
    }

    sendFeedback() {
      new Request(this.context.store, {
        method: 'GET',
        path: 'feedbackMessage?message=test&connectionId=' + this.props.call.connectionId
      }).send();
      this.context.store.dispatch({
        type: 'SCREEN_CHANGE',
        screen: 'SadFeedbackComplete'
      });
    }

    render() {
      return <View
        {...this.props}
        changeDecision={this.changeDecision.bind(this)}
        sendFeedback={this.sendFeedback.bind(this)}
      />;
    }

  }


