import React from 'react';

import '../../static/css/settings.css';

export default class extends React.Component {

  constructor(props) {
    super(props);
    this.clickTariff = this.clickTariff.bind(this);
  }

  clickTariff(id) {
    this.props.openTariff(id);
  }

  clickBuy(id) {
    this.props.buyTariff(id);
  }

  renderTariffs() {
    let caption;
    const items = [];
    const openedTariff = this.props.settings.openedTariff;
    if (this.props.tariff && this.props.tariff.items && this.props.tariff.items.tariffs) {
      for (let v of this.props.tariff.items.tariffs) {
        let openedClass = v.id === openedTariff ? ' opened' : '';
        if (!v.bought) {
          caption = <div className="caption activate"onClick={() => {
            this.clickBuy(v.id);
          }}> </div>;
        } else {
          caption = <div className={'caption' + (v.bought ? ' bought' : '')}>{v.price}</div>
        }
        items.push(
          <div className={'item' + openedClass} key={v.id}>
            <div className="title" onClick={() => {
              this.clickTariff(v.id);
            }}><span>{v.name}{/*v.bought ? ' ()' : ''*/}</span></div>
            {caption}
            <div className="description" dangerouslySetInnerHTML={{
              __html: (v.description ? v.description : '')  + `<p>${v.price}</p>`
            }}>
            </div>
          </div>
        );
      }
    }
    return <div className="items">{items}</div>;
  }

  renderBody() {
    if (this.props.settings.openedResult) {
      return <div
        dangerouslySetInnerHTML={{__html: this.props.settings.openedResult}}>
      </div>
    } else {
      return this.renderTariffs();
    }
  }

  render() {
    return <div className="body text">
      {this.renderBody()}
    </div>
  }

}



