import config from '../config';

export default {

  chatApiUrl: () => {
    return config.host + ':' + config.httpPort + '/';
  },

  chatWsUrl: () => {
    return config.wsHost + ':' + config.wsPort;
  },

  callerApiUrl: () => {
    return config.hmHost + ':' + config.hmPort + '/api/v1/';
  },

  callerUserToken: () => {
    return config.callerUserToken;
  },

  tariffApiUrl: () => {
    return config.tariffHost + ':' + config.tariffPort + '/';
  }

}


