import React, { Component } from "react";
import reduxConnect from "../core/utils/reduxConnect";
import getAuth from "../actions/auth/get";
import setAuth from "../actions/auth/set";
import initSocket from "../actions/socket";
import ucfirst from "ucfirst";

import bindStatusEmitter from "../actions/call/bindEmitter";
import StatusEmitter from "../core/utils/StatusEmitter";

import Chat from "./Chat";
import Phone from "./Phone";
import Settings from "./Settings";
import Calling from "./Calling";
import NoAnswer from "./NoAnswer";
import Talking from "./Talking";
import Hangup from "./Hangup";
import HappyFeedback from "./HappyFeedback";
import SadFeedback from "./SadFeedback";
import SadFeedbackComplete from "./SadFeedbackComplete";
import Error from "./Error";

import "../static/css/main.css";
import "../static/css/auth.css";
import "../static/css/popup.css";
import "../static/css/list.css";
import "../static/css/button.css";
import "../static/css/loading.css";

import logout from "../actions/logout";
import login from "../actions/login";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      socketInitialized: false,
      modalVisible: false
    };
    this.changeScreen = this.changeScreen.bind(this);
  }

  componentDidUpdate() {
    if (this.props.call.waitForStatus) {
      this.emitter.start(this.props.call.connectionId);
    } else {
      this.emitter.stop();
    }
    this.initSocket();
  }

  initSocket() {
    if (this.state.socketInitialized) {
      return;
    }
    const state = this.context.store.getState();
    if (state.auth.token) {
      initSocket(this.context.store, state.auth.token);
      this.setState({
        socketInitialized: true
      });
    }
  }

  initParseQuery() {
    //console.log('location', document.location);
    let params = new URL(document.location.href);
    const pairs = params.search.substr(1).split("&");
    const queryObj = {};
    for (let p of pairs) {
      const arr = p.split("=");
      queryObj[arr[0]] = arr[1];
    }

    this.context.store.dispatch({
      type: "SET_GROUP_ID",
      groupId: queryObj.groupId
    });

    this.context.store.dispatch({
      type: "SET_EVOTOR_TOKEN",
      token: queryObj.externalToken
    });
  }

  initAuthentication() {
    if (window.location.hash === "#logout") {
      logout(this.context.store.dispatch); //  
      window.location.hash = "";
      window.location.reload(true);
      return;
    }
    let token = false;
    if (window.location.hash.match(/#token.*/)) {
      token = window.location.hash.replace(/#token(.*)/, "$1");
    }
    const auth = getAuth();
    const store = this.context.store.getState();

    //       ,    ,    
    if (store.auth.evotorToken) {
      logout(this.context.store.dispatch); //  
      //  
      login(this.context.store, { token: store.auth.evotorToken });
    } else if (token) {
      //    jwt 
      logout(this.context.store.dispatch); //  
      //  
      login(this.context.store, { token });
    } else if (auth) {
      setAuth(this.context.store, auth);
    } else {
      logout(this.context.store.dispatch); //  

      login(this.context.store, { token: false });
    }
  }

  changeScreen(event) {
    switch (event.data) {
      case "Chat":
        this.context.store.dispatch({
          type: "SCREEN_CHANGE",
          screen: "Chat"
        });
        this.context.store.dispatch({
          type: "SECTION_CHANGE",
          section: "chat"
        });
        return true;
      case "Phone":
        this.context.store.dispatch({
          type: "SCREEN_CHANGE",
          screen: "Phone"
        });
        this.context.store.dispatch({
          type: "SECTION_CHANGE",
          section: "call"
        });
        return true;
      case "Settings":
        this.context.store.dispatch({
          type: "SCREEN_CHANGE",
          screen: "Settings"
        });
        this.context.store.dispatch({
          type: "SECTION_CHANGE",
          section: "settings"
        });
        return true;
      default:
        return false;
    }
  }

  componentDidMount() {
    global.window.addEventListener("message", this.changeScreen, false);
    this.initParseQuery(), this.initAuthentication();
    this.context.store.dispatch({ type: "SCREEN_INIT" });
    this.context.store.dispatch({ type: "SECTION_INIT" });
    this.context.store.dispatch({ type: "SET_LOADING", loading: false });
    this.context.store.dispatch({
      type: "PHONE_CHANGE",
      phone: ""
    });

    // this.context.store.dispatch({
    //   type: 'SET_CONNECTION_ID',
    //   connectionId: 123,
    // });
    //
    // setTimeout(() => {
    //   this.context.store.dispatch({
    //     type: 'SCREEN_CHANGE',
    //     screen: 'Hangup'
    //   });
    // }, 1000);

    this.emitter = new StatusEmitter(this.context.store);
    bindStatusEmitter(this.context.store, this.emitter);

    if (window.location.hash.length > 1) {
      const hash = window.location.hash.replace(/#(.*)/, "$1");
      if (hash === "call" || hash === "settings" || hash === "chat") {
        this.clickMenu(hash);
      }
    }
  }

  clickMenu(section) {
    let screen;
    if (section === "call") {
      screen = "Phone";
    } else {
      screen = ucfirst(section);
    }
    this.context.store.dispatch({
      type: "SCREEN_CHANGE",
      screen
    });
    this.context.store.dispatch({
      type: "SECTION_CHANGE",
      section
    });
  }

  renderMenu() {
    let links = [];
    let curSection = this.context.store.getState().navigation.section;
    const sections = ["chat", "call", "settings"];
    for (let section of sections) {
      let active = curSection === section ? " active" : "";
      links.push(
        <a
          key={section}
          href="#"
          className={"m-" + section + active}
          onClick={e => {
            e.preventDefault();
            this.clickMenu(section);
          }}
        />
      );
    }
    return <div className="menu">{links}</div>;
  }

  renderScreen() {
    const screen = this.context.store.getState().navigation.screen;
    switch (screen) {
      case "Chat":
        return <Chat />;
      case "Phone":
        return <Phone />;
      case "Settings":
        return <Settings />;
      case "Calling":
        return <Calling />;
      case "NoAnswer":
        return <NoAnswer />;
      case "Talking":
        return <Talking />;
      case "Hangup":
        return <Hangup />;
      case "HappyFeedback":
        return <HappyFeedback />;
      case "SadFeedback":
        return <SadFeedback />;
      case "SadFeedbackComplete":
        return <SadFeedbackComplete />;
      case "Error":
        return <Error />;
      default:
        console.trace(screen);
        throw new Error('ScreenComponent for "' + screen + '" is not defined');
    }
  }

  renderAuth() {
    const state = this.context.store.getState();
    if (state.navigation.screen === "Error") {
      return <Error />;
    } else {
      return "  ";
    }
  }
  componentWillUnmount() {
    global.window.removeEventListener("message");
  }
  render() {
    const state = this.context.store.getState();
    const section = state.navigation.section;
    const screen = state.navigation.screen;
    const loading = state.request.loading;
    if (!this.props.auth.token) {
      return (
        <div className="pageWrapper loginPage">
          <div className="block">{this.renderAuth()}</div>
        </div>
      );
    } else {
      return (
        <div
          className={
            "section-" +
            section +
            " screen screen" +
            screen +
            (loading ? " loading" : "")
          }
        >
          {/*<div className="header">
          <div className="hm"></div>
          <div className="lifeline"></div>
          <div className="text">
                1 
          </div>
        </div>
        this.renderMenu()*/}
          {this.renderScreen()}
        </div>
      );
    }
  }
}

export default reduxConnect(App);



