import ChatRequest from "../core/utils/request/ChatRequest";
import setAuth from "./auth/set";

export default (store, params) => {
  //console.log('lgin', params);
  // console.log('stor', store);
  new ChatRequest(
    store,
    {
      method: "post",
      path: params.token
        ? "client/authorize/externaltoken"
        : "client/authorize/anon",
      data: params
    },
    {
      onSuccess: data => {
        setAuth(store, data, !params.token);
      }
    }
  ).send();
};



