import RequestAbstract from './RequestAbstract';
import server from '../server';

export default class extends RequestAbstract {

  baseUrl() {
    return server.chatApiUrl();
  }

  handleResponse(response) {
    if (response.data === 'Incorrect token') {
      this.error({
        text: '  '
      });
      return;
    }
    super.handleResponse(response);
  }

}


