import React from "react";
import formatDate from "../../core/utils/date";

function ChatMessageWeb(props) {
  const { text, mine, hideDate, dt, author } = props;
  const messageDate = hideDate ? "" : formatDate(dt);
  const messageClass = mine ? "mine " : "";
  const messageAvatar = !mine ? (
    <chat-message-avatar>
      <img
        src={`http://5.200.55.167/i/helpme/oper/${author._id}.png`}
        onError={event => {
          event.target.parentNode.classList.add("no-avatar");
        }}
        alt=""
      />
    </chat-message-avatar>
  ) : (
    ""
  );

  return (
    <chat-message class={messageClass}>
      {messageAvatar}
      <chat-message-text>{text}</chat-message-text>
      <chat-message-date>{messageDate}</chat-message-date>
    </chat-message>
  );
}

export default ChatMessageWeb;



