import moment from "moment";
import "moment/locale/ru";
moment.locale("ru");

export default date => {
  const currentDate = moment(date);
  //console.log(": ", date);

  if (currentDate.diff(moment(), "days") === 0) {
    //   ,    
    return currentDate.format("h:mm");
  } else {
    return currentDate.format("h:mm, D MMM");
  }
};



