import React from 'react';

import '../../static/css/call.css';

export default class extends React.Component {

  constructor(props) {
    super(props);
    this.handleKeyup = this.handleKeyup.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleKeyup(e) {
    if (e.keyCode === 13) {
      this.props.callAndGoToScreenCalling();
    }
  }

  handleChange(e) {
    this.props.phoneChanged(e.target.value);
  }

  renderButton() {
    return this.props.validate() ?
      <button
        onClick={this.props.callAndGoToScreenCalling}>
        <i className="fa fa-phone" />
      </button>
      : <button
        disabled
        onClick={this.props.callAndGoToScreenCalling}>
        <i className="fa fa-phone" />
      </button>;
  }

  render() {
    return <div className="body">
      <div className="woman"></div>
      <div className="text">
        <span>        </span>
      </div>
      <div className="input">
        {this.renderButton()}
        <input
          value={this.props.phone.phone}
          type="text"
          placeholder="+7"
          onChange={this.handleChange}
          onKeyUp={this.handleKeyup}
        />
      </div>
      {/* <div className="appBar">
        <div className="appText">
          <span>  HelpMe     </span>
        </div>
        <div className="getApp">
          <div className="qr"></div>
          <a href="https://play.google.com/store/apps/details?id=helpMe.client&hl=ru" className="googlePlay" target="blank"></a>
        </div>
      </div> */}
    </div>
  }

}



