import ChatAuthRequest from "../../core/utils/request/ChatAuthRequest";

export default (store, text, onSuccess) => {
  store.dispatch({
    type: "SET_SENDING",
    sending: true
  });
  new ChatAuthRequest(
    store,
    {
      method: "put",
      path: "client/messages",
      data: {
        text,
        groupId: store.getState().group.id
      }
    },
    {
      onSuccess: data => {
        store.dispatch({
          type: "SET_SENDING",
          sending: false
        });
        store.dispatch({
          type: "SET_CURRENT_CHAT_LINES",
          lines: [data.message.line]
        });
        store.dispatch({
          type: "NEW_MESSAGE",
          message: data.message
        });
        onSuccess();
      }
    }
  ).send();
};



