import React from 'react';
import '../../static/css/error.css';

export default class extends React.Component {

  render() {
    return <div className="body">
      <p>{this.props.navigation.text}</p>
      {(this.props.navigation.subText ?
          <div className="gray">
            {this.props.navigation.subText}
          </div>
          : ''
      )}
    </div>
  }

}



