import React from 'react';
import axios from 'axios';
import server from '../server';

export default class Abstract {

  baseUrl() {
    throw new TypeError('Abstract');
  }

  constructor(store, data, options) {
    this.store = store;
    data.url = this.baseUrl() + data.path;
    this.options = Object.assign({
      onSuccess: null,
      onNoConnection: null,
      onRetry: null,
      onError: null
    }, options);
    this.currentScreen = this.store.getState().navigation.screen;
    this.data = data;
  }

  error(err) {
    if (this.options.onError) this.options.onError(err);
    if (err.subText === undefined) err.subText = '';
    this.store.dispatch(Object.assign({
      type: 'SCREEN_CHANGE',
      screen: 'Error',
    }, err));
  }

  noConnection() {
    if (!this.options.onSuccess) {
      //     ,     
      return;
    }
    if (this.options.onNoConnection) this.options.onNoConnection();
    this.error({
      text: '       ',
      subText: <div className="form">
        <button className="button" onClick={this.retry.bind(this)}> </button>
      </div>
    });
  }

  retry() {
    if (this.options.onRetry) this.options.onRetry();
    this.send();
  }

  notFound(status) {
    const codes = {
      400: 'Bad Request',
      401: '  ',
      403: 'Forbidden',
      404: 'Not Found'
    };
    let subText;
    if (codes[status]) {
      subText = status + ' ' + codes[status];
    } else {
      subText = ' : ' + status;
    }
    this.error({
      text: '  ',
      subText: subText
    });
  }

  handleResponse(response) {
    if (response.data && response.data.error) {
      this.error({
        text: '  ',
        subText: response.data.error
      });
      return;
    }
    if (this.options.onSuccess) {
      this.options.onSuccess(response.data);
      this.onSuccess();
    }
  }

  onSuccess() {
    // console.log('this.currentScreen', this.currentScreen);
    // this.store.dispatch({
    //   type: 'SCREEN_CHANGE',
    //   screen: this.currentScreen
    // });
  }

  send() {
    this.store.dispatch({
      type: 'SET_LOADING',
      loading: true
    });
    axios(this.data).then((response) => {
      this.store.dispatch({
        type: 'SET_LOADING',
        loading: false
      });
      if (!response) {
        this.noConnection();
        return;
      }
      this.handleResponse(response)
    }).catch((error) => {
      this.store.dispatch({
        type: 'SET_LOADING',
        loading: false
      });
      //console.log(error);
      throw error;
      if (!error.response) {
        this.noConnection();
      } else {
        if (error.response.status.toString().charAt(0) === '4') {
          this.notFound(error.response.status);
        } else {
          this.error({
            text: '  ',
            subText: error.message
          });
        }
      }
    });
  }
};



