import RequestAbstract from './RequestAbstract';
import server from '../server';

export default class extends RequestAbstract {

  baseUrl() {
    return server.tariffApiUrl();
  }

  constructor(store, data, options) {
    super(store, data, options);
    if (!store.getState().auth.cloudToken) {
      throw new Error('Cloud not authorized');
    }
    this.data.headers = {
      'Authorization': 'Bearer ' + store.getState().auth.cloudToken
    };
  }

}


