export default (state = {}, action) => {
  let newState, currentMessages;

  switch (action.type) {
    case "SET_SENDING":
      return Object.assign({}, state, {
        sending: action.sending ? true : false
      });

    case "INIT_MESSAGES":
      return Object.assign({}, state, {
        sending: false,
        messages: {}
      });

    case "SET_MESSAGES":
      return Object.assign({}, state, {
        messages: action.messages
      });

    case "NEW_MESSAGE":
      if (state.lastMessageId && state.lastMessageId === action.message._id) {
        return state;
      }

      //console.log("state here", state);
      // console.log("action.message", action.message);

      if (
        !state.currentLines ||
        state.currentLines.indexOf(action.message.line) === -1
      ) {
        return state;
      }

      //console.log("pushing");

      newState = Object.assign({}, state);
      newState.lastMessageId = action.message._id;
      newState.messages.push(action.message);
      return newState;

    case "SET_INCIDENTS":
      return Object.assign({}, state, {
        incidents: action.incidents
      });

    case "ADD_INCIDENT":
      newState = Object.assign({}, state);
      newState.incidents.push(action.incident);
      return newState;

    case "UPDATE_INCIDENT_TAGS":
      newState = Object.assign({}, state);
      for (let i = 0; i < newState.incidents.length; i++) {
        if (newState.incidents[i].resolved === false) {
          newState.incidents[i].tags = action.tags;
        }
      }
      return newState;

    case "SET_CURRENT_CHAT_LINES":
      if (!Array.isArray(action.lines)) {
        action.lines = [action.lines];
      }

      return Object.assign({}, state, {
        currentLines: action.lines
      });

    default:
      return state;
  }
};



