import React from 'react';
import Request from '../utils/request/CallerRequest';

export default (View) =>
  class extends React.Component {

    like() {
      this.context.store.dispatch({
        type: 'SCREEN_CHANGE',
        screen: 'HappyFeedback'
      });
      this.request(true);
    }

    dislike() {
      this.context.store.dispatch({
        type: 'SCREEN_CHANGE',
        screen: 'SadFeedback'
      });
      this.request(false);
    }

    render() {
      return <View
        {...this.props}
        like={this.like.bind(this)}
        dislike={this.dislike.bind(this)}
      />;
    }

    request(positive) {
      new Request(this.context.store, {
        method: 'GET',
        path: 'feedback?positive=' + (positive ? 1 : 0) + '&connectionId=' + this.props.call.connectionId
      }).send();
    }

  }




