import server from "../core/utils/server";
import newMessage from "./message/newMessage";

export default (store, userToken) => {
  const socket = require("socket.io-client")(server.chatWsUrl());

  socket.on("connect", function() {
    //console.log('connect, emitting', userToken);
    socket.emit("clientConnect", {
      token: userToken
    });
  });

  socket.on("newMessage", function(message) {
    // console.log('newMessage', message)
    newMessage(userToken, store.getState(), store.dispatch, message);
  });

  socket.on("newIncident", function(incident) {
    //console.log('newIncident', incident);
    store.dispatch(
      Object.assign(
        {
          type: "ADD_INCIDENT"
        },
        { incident }
      )
    );
  });

  socket.on("incidentTagsChanged", function(data) {
    //console.log('incidentTagsChanged', data);
    store.dispatch(
      Object.assign(
        {
          type: "UPDATE_INCIDENT_TAGS"
        },
        data
      )
    );
  });
};



