import React, { Component } from "react";
import Dropzone from "react-dropzone";
import request from "superagent";

const CLOUDINARY_UPLOAD_PRESET = "bmzjbxoq";
const CLOUDINARY_UPLOAD_URL =
  "https://api.cloudinary.com/v1_1/react-cloudinary/upload";

class FileUploader extends Component {
  state = {
    uploadedFiles: []
  };

  render() {
    const { uploadedFiles } = this.state;
    const { id } = this.props;

    const dropFiles = uploadedFiles.map(el => {
      const elemID = this.getShortID(el.preview);
      return (
        <drop-file key={elemID} id={elemID}>
          <drop-file-img>
            <div className="close" onClick={this.deleteFile} />
            <img src={el.url} alt={el.name} />
            <buttons-container>
              <open-file />
            </buttons-container>
          </drop-file-img>
          <p>{el.name}</p>
        </drop-file>
      );
    });

    return (
      <React.Fragment>
        <Dropzone
          onDrop={this.onImageDrop}
          multiple={true}
          className="drop-zone"
          id={id}
        />

        {uploadedFiles !== [] ? <drop-files>{dropFiles}</drop-files> : null}
      </React.Fragment>
    );
  }

  componentDidMount() {
    const { id } = this.props,
      dropzone = document.getElementById(id),
      input = dropzone.getElementsByTagName("input")[0];

    input.addEventListener(
      "mouseover",
      event => {
        event.preventDefault();
        dropzone.classList.add("show");
      },
      true
    );

    dropzone.parentNode.addEventListener("mouseout", () => {
      dropzone.classList.remove("show");
    });
  }

  getShortID = longID => longID.slice(-6);

  onImageDrop = files => {
    const uploadFiles = [...this.state.uploadedFiles, files[0]];

    this.setState({
      uploadedFiles: uploadFiles
    });

    this.handleImageUpload(files[0], uploadFiles.length);
  };

  handleImageUpload(file, index) {
    let upload = request
      .post(CLOUDINARY_UPLOAD_URL)
      .field("upload_preset", CLOUDINARY_UPLOAD_PRESET)
      .field("file", file);

    upload.end((err, response) => {
      if (err) {
        console.error(err);
      }

      if (response.body.secure_url !== "") {
        let newArr = this.state.uploadedFiles;
        newArr[index - 1].url = response.body.secure_url;

        this.setState({
          uploadedFile: newArr
        });
      }
    });
  }

  deleteFile = event => {
    const id = event.target.closest("drop-file").id,
      files = [...this.state.uploadedFiles];

    this.setState({
      uploadedFiles: files.filter(
        (el, index) => this.getShortID(el.preview) !== id
      )
    });
  };
}

export default FileUploader;



