import React, { Component } from "react";
import ChatMessage from "./ChatMessageWeb";
import FileUploader from "./../FileUploader";
import autosize from "autosize";
import { Scrollbars } from "react-custom-scrollbars";

import "../../static/css/chat.css";

class ChatWeb extends Component {
  componentDidMount() {
    document
      .querySelector(".section-chat")
      .closest("#root")
      .classList.add("chat-page");
  }

  render() {
    if (this.props.chat.loading !== false) return <div />;

    return (
      <div className="chat">
        {ChatHeader()}
        <ChatMessages {...this.props} />
        <ChatTextarea {...this.props} />
      </div>
    );
  }
}

function ChatHeader() {
  return (
    <chat-header>
      <chat-header-img className="avatar">
        <img
          src={`http://5.200.55.167/i/helpme/oper/87657567567.png`}
          onError={event => {
            event.target.parentNode.classList.add("no-avatar");
          }}
          alt=""
        />
      </chat-header-img>
      <chat-header-text>
        <chat-header-title> </chat-header-title>
        <chat-header-subtitle> </chat-header-subtitle>
      </chat-header-text>
      <chat-header-link>
        <a
          target="_blank"
          rel="noopener noreferrer"
          href="https://docs.google.com/document/d/158YVhj2TqlkNVUzPJG2sX4XrCDQBZGg01aZ-ut5fSPQ"
        >
            
        </a>
      </chat-header-link>
    </chat-header>
  );
}

class ChatTextarea extends Component {
  state = {
    text: ""
  };

  render() {
    return (
      <div className="form-for-send-message">
        <textarea
          onChange={this.textChanged.bind(this)}
          onKeyPress={this.handleKeyPress}
          placeholder=" ..."
          value={this.state.text}
          autoFocus={true}
        />
        {/*<FileUploader id="chat-form-dropzone" />*/}
      </div>
    );
  }

  componentDidMount() {
    autosize(document.querySelectorAll(".form-for-send-message textarea"));
  }

  componentWillUpdate() {
    autosize.update(
      document.querySelectorAll(".form-for-send-message textarea")
    );
  }

  handleKeyPress = event => {
    if (event.key === "Enter" && !event.shiftKey && this.state.text !== "") {
      event.preventDefault();
      this.props.send(this.state.text);
      this.setState({
        text: ""
      });
    }
  };

  textChanged = event => {
    event.preventDefault();
    this.setState({
      text: event.target.value
    });
  };
}

class ChatMessages extends Component {
  render() {
    if (this.props.chat.loading !== false) return <div />;

    return (
      <Scrollbars
        ref={c => {
          this.scrollComponent = c;
        }}
      >
        <div className="chatMessages" id="chatMessages">
          {this.renderMessages()}
        </div>
      </Scrollbars>
    );
  }

  componentDidMount() {
    this.scrollToBottom(1);
  }

  componentWillUpdate() {
    if (this.scrollComponent && this.scrollComponent.getValues().top === 1) {
      this.scrollToBottom(100);
    }
  }

  scrollToBottom = time => {
    if (this.scrollComponent) {
      setTimeout(() => {
        this.scrollComponent.scrollToBottom();
      }, time);
    }
  };

  renderMessages = () => {
    const { messages, incidents, auth } = this.props;
    const rows = [];
    let i = 0;
    let message;
    //let hideDate;
    let firstIncidentShown = false;
    let lastIncident = false;
    for (let j = 0; j < messages.length; j++) {
      message = messages[j];
      if (incidents[message._id]) {
        i++;
        rows.push(this.renderIncident(incidents[message._id], i));
        if (firstIncidentShown === false) firstIncidentShown = true;
      } else if (firstIncidentShown === false) {
        i++;
        lastIncident = this.findIncident(message.dt);
        if (lastIncident) {
          rows.push(this.renderIncident(lastIncident, i));
          firstIncidentShown = true;
        }
      }
      i++;
      //hideDate = false;
      if (messages[j + 1] !== undefined) {
        if (messages[j + 1]) {
          if (messages[j + 1].author._id === message.author._id) {
            // hideDate = true;
          }
        }
      }
      rows.push(
        <ChatMessage
          mine={auth.profileId === message.author._id}
          key={i}
          // hideDate={hideDate}
          {...message}
        />
      );
    }
    return rows;
  };

  findIncident = messageDate => {
    const { incidents } = this.props;
    for (let id in incidents) {
      if (incidents[id].dt >= messageDate) {
        return incidents[id];
      }
    }
    return false;
  };

  renderIncident = (incident, i) => {
    const label =
      incident.tags && incident.tags.length > 0 ? (
        <span>{incident.tags.join(", ")}</span>
      ) : (
        ""
      );
    return (
      <li className="startIncident" key={i}>
        <div>{label}</div>
      </li>
    );
  };
}

export default ChatWeb;



