import ChatAuthRequest from "../../core/utils/request/ChatAuthRequest";

export default store => {
  store.dispatch({
    type: "UPDATE_CHAT"
  });

  new ChatAuthRequest(
    store,
    {
      method: "get",
      path: "client/incidents"
    },
    {
      onSuccess: incidents => {
        store.dispatch({
          type: "SET_INCIDENTS",
          incidents
        });
      }
    }
  ).send();

  new ChatAuthRequest(
    store,
    {
      method: "get",
      path: "client/messages",
      params: {
        groupId: store.getState().group.id
      }
    },
    {
      onSuccess: data => {
        /*
               
                
        */
        const lines = {};
        data.map(el => {
          lines[el.chat] = true;
        });

        store.dispatch({
          type: "SET_CURRENT_CHAT_LINES",
          lines: Object.keys(lines)
        });

        store.dispatch({
          type: "SET_MESSAGES",
          messages: data
        });
        store.dispatch({
          type: "CHAT_LOADED"
        });
      }
    }
  ).send();
};



