export default (state = {}, action) => {

    switch (action.type) {
        case 'SET_EVOTOR_TOKEN':
        return Object.assign({}, state, { evotorToken: action.token });
        case 'SET_AUTH':
            return Object.assign({}, state, action);
        case 'LOGOUT':
            let _state = Object.assign({}, state);
            delete _state.token;
            return _state;
        default:
            return state;
    }

};


