import config from '../../core/config';

export default (store, auth, save = false) => {
  if (save) {
    window.localStorage.setItem('auth' + config.storageSuffix, JSON.stringify(auth));
  }
  store.dispatch(Object.assign({type: 'SET_AUTH'}, auth));
};


