import TariffRequest from '../../core/utils/request/TariffRequest';

export default (store) => {
  new TariffRequest(store, {
    method: 'get',
    path: `apps/types/HelpMe/tariffs`,
  }, {
    onSuccess: (items) => {
      store.dispatch(Object.assign({type: 'SET_TARIFFS'}, {items}));
    }
  }).send();
};



